.content {
    height: auto;
    margin: 50px auto;

    .category-wrapper {
        margin-bottom: 115px;

        .item {
            margin-top: 30px;

            .item-inner {
                margin-bottom: 40px; }

            .item-title {
                line-height: 1.5em;
                font-weight: 600;
                font-stretch: ultra-condensed; }

            .item-date {
                color: lighten($color, 35%);
                font-size: .8em;
                font-weight: 300;
                margin-bottom: 0; }

            .item-description {
                font-weight: 300; }

            &.item-block {
                .img-pinned {
                    width: 100%;
                    max-height: 400px;
                    height: auto;
                    object-fit: cover; } }

            &.item-inline {

                .post-img {
                    background-position: center center;
                    object-fit: cover;
                    min-height: 200px;
                    max-height: 200px;
                    min-width: 328px;
                    width: 100%;
                    margin-bottom: 20px; } }

            &.dual {
                .post-img {
                    min-height: 300px;
                    max-height: 300px; } }

            &.multiple {

                [class^='col-md-'] {
                    margin: 30px auto; }

                .post-owner {
                    .col-sm-10 {
                        padding-left: 35px; } } } }

        .load-more {
            width: 100%;
            margin: 50px auto;
            text-align: center; } } }
