#articles {
    font-weight: 300;
    margin-top: 50px;

    [id^='group-'] {
        margin-top: 50px; }

    .wrap {
        background-color: #fff;
        border-radius: 3px;
        box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.2);
        min-width: 100%;
        width: auto;
        height: auto;
        margin-bottom: 20px;
        overflow: hidden;

        .post-title {
            font-weight: 600; }

        .post-meta {
            max-height: 128px;
            height: 128px;
            overflow: hidden;

            .post-title {
                margin-bottom: 10px;
                font-size: 1.1em;
                line-height: 25px;
                max-height: 50px;
                overflow: hidden; }

            .post-subtitle {
                font-size: .9em;
                color: $color-lighten;
                line-height: 25px;
                max-height: 50px;
                overflow: hidden; } }

        &.pinned {
            padding: 270px 40px 40px;
            background-repeat: no-repeat !important;
            background-position: 50% 50% !important;
            height: 460px;

            .post-title,
            .post-subtitle {
                color: $color-inverse; } }

        &.no-picture {
            padding: 20px;

            .post-owner-figure {
                border: 1px solid lighten($base-color, 20%); }

            .meta-wrapper {
                .item-date {
                    color: $color-lighten; } } }

        &.inline-picture {
            padding-left: 0;

            .content-wrapper {
                padding: 20px; } }

        .tint {
            position: relative;
            float: left;
            margin-right: 20px;
            margin-bottom: 20px;

            &:before {
                content: "";
                display: block;
                position: absolute;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;
                background: rgba(0,255,255, 0.5);
                transition: all .3s linear; }

            &:hover {
                &:before {
                    background: none; } } } } }

