small {
    font-weight: 300 !important; }

a {
    color: $base-color;
    text-decoration: none;
    transition: all .4s ease;
    outline: 0;

    &.no-color {
        color: $color; }

    &:hover {
        text-decoration: none; } }

a:hover:not(.btn),
a:active:not(.btn),
a:focus:not(.btn) {
    text-decoration: none;
    color: lighten($base-color, 10%);
    transition: all .4s ease; }

.container:not(.post-owner) {
    a {
        color: $color; }

    a:hover,
    a:active,
    a:focus {
        color: lighten($color, 10%);
        transition: none; } }

.post-owner,
.load-more {
    a {
        color: $base-color !important; } }

.text-uppercase {
    text-transform: uppercase; }

.text-lowercase {
    text-transform: lowercase; }

.text-capitalize {
    text-transform: capitalize; }

.text-normal {
    text-transform: none; }

.text-semi-condensed {
    font-stretch: semi-condensed; }

.text-condensed {
    font-stretch: condensed; }

.text-base {
    color: $base-color; }

.text-grey {
    color: $color-lighten; }

.text-light {
    font-weight: 300; }

.shadowed {
    box-shadow: 0 4px 4px -4px $base-shadow; }

.category {
    background-color: $base-background;
    text-align: center;
    padding: 5px 30px;
    position: relative;
    z-index: 1000;
    color: $base-color;
    top: -5px;
    font-size: 1.4em; }

.category-separator {
    width: 100%;
    height: 13px;
    border-bottom: 1px solid $base-color;
    text-align: center; }
